import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import _ from 'lodash';

const bynPath = 'https://developerhub.alfabank.by:8273/partner/1.0.1/public/nationalRates';
const plnPath = 'https://api.nbp.pl/api/exchangerates/tables/c?format=json';

function App() {
  const [byn, setByn] = useState({ eur: 0, usd: 0 });
  const [pln, setPln] = useState({ eur: 0, usd: 0 });

  useEffect(() => {
    (async () => {
      try {
        const response = await axios.get(bynPath);
        const { rates } = response.data;
        const bynEur = _.find(rates, { iso: 'EUR' });
        const bynUsd = _.find(rates, { iso: 'USD' });
        setByn({
          eur: bynEur.rate,
          usd: bynUsd.rate,
        });
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  useEffect(() => {
    (async () => {
      try {
        const response = await axios.get(plnPath);
        const { rates } = response.data[0];
        const plnEur = _.find(rates, { code: 'EUR' });
        const plnUsd = _.find(rates, { code: 'USD' });
        setPln({
          eur: plnEur.bid,
          usd: plnUsd.bid,
        });
      } catch (error) {
        console.error(error);
      }
    })();
  }, []);

  const bynEur = 3725 / byn.eur;
  const eurPln = bynEur * pln.eur;

  const bynUsd = 3725 / byn.usd;
  const usdPln = bynUsd * pln.usd;

  return (
    <div>
      <div>
        3725 BYN = {bynEur} EUR = {eurPln} PLN
      </div>
      <div>
        3725 BYN = {bynUsd} USD = {usdPln} PLN
      </div>
    </div>
  );
}

export default App;
